.modal_footer__backdrop {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: rgba(47, 48, 58, 0.4);

    opacity: 1;
    visibility: visible;
    pointer-events: initial;

    transition: opacity 250ms linear, visibility 250ms linear;
}

.modal_footer__backdrop.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.modal_footer {
    position: absolute;
    padding: 25px;
    width: 300px;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);
    background-color: white;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
    transition: transform 250ms;

    
        @media screen and (min-width: 768px) {   
        width: 515px;
        }
    
        @media screen and (min-width: 1280px) {
        width: 964px;
        }
    
}

.modal_footer__backdrop.isl-hidden .modal {
    transform: translate(-50%, -75%);
}

.modal_footer__close-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 25px;
    height: 25px;
    padding: 0;
    top: 8px;
    right: 8px;
    cursor: pointer;

    border-radius: 50%;
    background: #ffffff;
}

.modal_footer__close-btn-icon {
    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.modal_footer__close-btn:hover .modal_footer__close-btn-icon,
.modal_footer__close-btn:focus .modal_footer__close-btn-icon {
    fill: rgb(185, 24, 24);
}

