.modal_footer__team-list {
    
    background-color: #ffffff;
    display: inline-block;
    text-align: center;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
    margin-right: 20px;
    margin-bottom: 20px;

    @media screen and (max-width: 767px) {
        margin-right: 0;
        width: 250px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
        width: 220px;
        &:nth-child(-n+2) {
            // margin-bottom: 20px;
        }

        &:nth-child(even) {
            margin-right: 0px;
        }

        &:nth-child(3) {
            // margin-bottom: 0;
        }
    }

    @media screen and (min-width: 1280px) {
        width: 210px;

        &:nth-child(4n) {
            margin-right: 0;
        }

        &:last-child {
            margin-right: 0;
        }
        &:nth-child(n + 13) {
            margin-bottom: 0;
        }
    
    }
}

