.modal_footer__social-list {
    display: flex;
    gap: 5px;
    justify-content: center;
}

.modal_footer__social-list--item {
    width: 35px;
    height: 35px;
    background-position: center;
    overflow: hidden;
    border-radius: 50%;

}

.modal_footer__social-list--link {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #AFB1B8;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        color: #ffffff;
        background-color: #2196F3;
        border-radius: 50%;
    }
}

.modal_footer__social-list--icon {
    width: 20px;
    height: 20px;
    fill: currentColor;

}















