.loader__overlay {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .15s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.loader__overlay.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader--on {
  width: 100px;
  height: 100px;
  z-index: 999;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader--on div {
  box-sizing: border-box;
  width: 45px;
  height: 45px;
  border: 10px solid red;
  border-color: red #000;
  border-radius: 50%;
  margin: 10px;
  animation: loader 2s linear infinite;
  display: block;
  position: absolute;
}

.loader--on div:nth-child(1) {
  animation-delay: -450ms;
}

.loader--on div:nth-child(2) {
  animation-delay: -300ms;
}

.loader--on div:nth-child(3) {
  animation-delay: -150ms;
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100vw;
  height: 100vh;
  z-index: 1200;
  background-color: rgba(0, 0, 0, .8);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: calc(100% - 40px);
  background-color: #fff;
  outline: 2px solid #000;
  padding: 48px 20px;
